<template>
    <setup title="Setup Your Email" :error="error">
        <form method="post" class="card-body" @submit.prevent="handleFormSubmit">
            <div class="default-form w-border" style="text-align: left; margin: auto">
                <div class="form-row">
                    <input type="text" :placeholder="`Enter Work Email${!homeEmailRequired ? ' (Optional)' : '*'}`" v-model="WorkEmail" class="long" />
                </div>
                <div class="form-row">
                    <input type="text" placeholder="Enter Personal Email (Optional)" v-model="HomeEmail" class="long" />
                </div>
                <div class="form-row" style="text-align: center">
                    <div class="spinner small light" v-show="loading"></div>
                    <input type="submit" value="Continue" class="button large blue" v-show="!loading" v-cloak />
                </div>
            </div>
        </form>
    </setup>
</template>

<script>
    import Setup from "./Setup.vue";

    export default {
        name: "Emails",
        components: {
            Setup,
        },
        data() {
            return {
                error: "",
                loading: false,
                redirectUrl: "",
                WorkEmail: "",
                HomeEmail: "",
                requiresMfa: undefined,
                homeEmailRequired: false,
            };
        },
        methods: {
            async checkCompanyMfa() {
                const {requiresMfa, contactDetailsAvailable} = await this.$ajax.get(`${this.$apiUrl}/api/Setup/company-check`, {withCredentials: true});
                this.requiresMfa = requiresMfa;
                this.homeEmailRequired = contactDetailsAvailable;
            },
            handleFormSubmit() {
                let data = {
                    redirectUrl: this.redirectUrl,
                    HomeEmail: this.HomeEmail,
                    WorkEmail: this.WorkEmail,
                };
                this.error = "";

                if (this.homeEmailRequired && !data.WorkEmail) {
                    this.error = "Work email is required.";
                } else if (typeof data.WorkEmail != "undefined" && data.WorkEmail.length && !this.$validateEmail(data.WorkEmail)) {
                    this.error = "Work email is not valid.";
                } else if (typeof data.HomeEmail != "undefined" && data.HomeEmail.length && !this.$validateEmail(data.HomeEmail)) {
                    this.error = "Personal email is not valid.";
                }

                if (this.error != "") return;

                this.loading = true;

                this.$ajax
                    .post("/Setup/Emails", data)
                    .then(async (resp) => {
                        if (typeof resp.data != "undefined") {
                            const {Error, ClientRedirect, RedirectUrl: redirectUrl, RedirectPage} = resp.data;
                            if (Error != null) {
                                this.error = resp.data.Error;
                            } else if (ClientRedirect && redirectUrl.length) {
                                const {authenticated, mfaRequired, otpChannels} = await this.$checkMFACookie(redirectUrl);
                                const mfaNotSet = mfaRequired && !authenticated;
                                if (mfaNotSet) {
                                    this.$router.push({
                                        name: "MFA",
                                        params: {
                                            redirectUrl,
                                            otpChannels,
                                        },
                                    });
                                } else {
                                    window.location.href = redirectUrl;
                                }
                            } else if (RedirectPage != null) {
                                const queryString = redirectUrl != null ? {redirectUrl} : {};
                                this.$router.push({
                                    name: RedirectPage,
                                    query: queryString,
                                });
                            }
                        }
                    })
                    .catch(() => {})
                    .then(() => {
                        this.loading = false;
                    });
            },
        },

        mounted() {
            if (typeof this.$route.query != "undefined" && typeof this.$route.query.redirectUrl != "undefined") {
                this.redirectUrl = this.$route.query.redirectUrl;
            }
            console.log("Emails Page mount");
            this.checkCompanyMfa();
        },
    };
</script>
